h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

svg, a {
    transition: 0.3s all;
}

.body {
	color: $dark;
	font-family: $font-reg;
	font-size: 16px;
    overflow-x: hidden;
    line-height: 1.3;
    background-color: #ffffff;
    @include r(1080) {

    }
    @include r(440) {

    }
}

.container {
    width: 1070px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
}

.title {
    text-transform: uppercase;
    font-family: $font-title;
    font-weight: 500;
    font-size: 30px;
    &--center {
        text-align: center;
    }
    strong {
        color: $red-light;
    }
}

.text {
    h2, h3, h4, h5, h6 {
        font-weight: bold;
        font-family: $font-title;
        margin-bottom: 1rem;
        padding-left: 88px;
    }
    h2 {
        font-size: 30px;
        padding-left: 0px;
    }
    h3 {
        font-size: 26px;
    }
    h4 {
        font-size: 22px;
    }
    h5 {
        font-size: 18px;
    }
    img {
        display: block;
        width: 100%;
        height: auto;
        margin-top: 40px;
        margin-bottom: 40px;
    }
    p {
        margin-bottom: 1rem;
        padding-left: 88px;
    }
    blockquote {
        color: $red-light;
        font-family: $font-title;
        font-size: 30px;
        font-weight: 300;
        padding-left: 30px;
        padding-bottom: 32px;
        margin-left: 54px;
        border-left: 4px solid $red-light;
        border-bottom: 2px solid $red-light;
        margin-bottom: 41px;
        margin-top: 40px;
    }
    a {
        text-decoration: underline;
        color: $dark;
        &:hover {
            opacity: 0.7;
        }
    }
    strong,b {
        font-weight: bold;
    }
    i {
        font-style: italic;
    }
}

.layout {
    display: flex;
    justify-content: space-between;
    margin-bottom: 60px;
    &__main {
        width: calc(75% - 6px);
    }
    &__sidebar {
        width: calc(25% - 6px);
        padding-top: 58px;
        &--0 {
            padding-top: 0px;
        }
    }
    &--block {
        display: block;
    }
}

.sidebar {
    background-color: $blue;
    color: $light;
    padding: 24px 13px;
    &--light {
        background-color: #ffffff;
        color: $dark;
        padding: 0px;
    }
    &--margin {
        margin-bottom: 58px;
    }
    &__header {
        min-height: 58px;
    }
    &__title {
        margin-bottom: 10px;
    }
    &__article {
        text-decoration: none;
        color: $light;
        padding-top: 21px;
        padding-bottom: 21px;
        display: block;
        &:hover {
            opacity: 0.7;
        }
        &--light {
            padding: 0px;
            height: 294px;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            background-color: $light;
            position: relative;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            margin-bottom: 14px;
        }
    }
    &__time {
        margin-bottom: 5px;
    }
    &__name {
        &--light {
            background: rgba(26, 36, 45, 0.8);
            padding: 8px;
        }
    }
}

.time {
    color: $blue-light;
    display: block;
    font-family: $font-title;
    font-size: 16px;
}

.section {
    &__header {
        min-height: 58px;
    }
    &__content {
        display: flex;
        flex-wrap: wrap;
    }
}

.breadcrumbs {
    display: flex;
    align-items: center;
    margin-bottom: 45px;
    &__link {
        position: relative;
        text-transform: uppercase;
        text-decoration: none;
        font-family: $font-title;
        color: $dark;
        &:after {
            content: "";
            position: absolute;
            top: 100%;
            left: 0px;
            right: 0px;
            background-color: $blue-light;
            height: 2px;
        }
        &:hover {
            color: $blue-light;
        }
        &--static {
            color: $blue-light;
            &:after {
                display: none;
            }
        }
    }
    svg {
        height: 7px;
        width: 5px;
        display: block;
        margin-left: 15px;
        margin-right: 15px;
    }
}

.break {
    flex-basis: 100%;
    height: 0;
}